:root{
  --surface0:#0a0f14;
  --surface1:#0d1117;
  --hair:rgba(148,163,184,.16);
  --ink:rgba(255,255,255,.88);
  --ink-weak:rgba(255,255,255,.72);
  --violet:#a855f7;
  --emerald:#22c55e;
}

/* ---------- page wrap + background ---------- */
.adminContainer{
  position: relative;
  min-height: calc(100dvh - 120px);
  background:
    radial-gradient(1100px 700px at 10% -10%, rgba(168,85,247,.08), transparent 60%),
    radial-gradient(1100px 700px at 110% 120%, rgba(34,197,94,.08), transparent 60%),
    linear-gradient(180deg, var(--surface1), var(--surface0));
  overflow: hidden;
  padding: 44px 16px 73px;
}

.inner{ width:min(1180px, 100%); margin:0 auto; position:relative; z-index:1; }

.header{ text-align:center; margin-bottom:32px; }
.title{ margin:0; color:#fff; font-weight:800; letter-spacing:.2px; font-size:2rem; }
.sub{ margin:.3rem 0 0; color:var(--ink-weak); font-size:1.1rem; }

.bubbles{ position:absolute; inset:0; margin:0; padding:0; list-style:none; pointer-events:none; z-index:0; }
.bubble{ position:absolute; left:var(--left); width:var(--size); height:var(--size); border-radius:50%; opacity:.5; }
.bubble::before{
  content:""; position:absolute; inset:0; border-radius:inherit;
  background:
    radial-gradient(circle at 30% 30%, rgba(168,85,247,.55), rgba(168,85,247,0) 60%),
    radial-gradient(circle at 70% 70%, rgba(34,197,94,.35), rgba(34,197,94,0) 55%),
    radial-gradient(circle at 45% 80%, rgba(56,189,248,.24), rgba(56,189,248,0) 52%);
  box-shadow:0 0 22px rgba(168,85,247,.18), 0 0 36px rgba(34,197,94,.14);
  filter:saturate(115%);
}
.rise{ bottom:-16vh; animation: rise var(--rise) linear infinite, sway var(--sway) ease-in-out infinite alternate; animation-delay: var(--delay); }

.fall{
  top:-16vh;
  animation: fall var(--fall) linear infinite,
             sway var(--sway) ease-in-out infinite alternate;
  animation-delay: var(--delay);
}

@keyframes fall{
  0%   { transform: translateY(-10vh) scale(.95); opacity:0; }
  10%  { opacity:.50; }
  100% { transform: translateY(115vh) scale(1.1); opacity:0; }
}

.float{
  top: var(--top);
  animation:
    floatY var(--bob)   ease-in-out infinite alternate,
    floatX var(--drift) ease-in-out infinite alternate,
    spin   var(--spin)  linear       infinite;
  animation-delay: var(--delay);
}

@keyframes floatX { 0% { transform: translateX(-14px); } 100% { transform: translateX(14px); } }
@keyframes floatY { 0% { transform: translateY(-10px); } 100% { transform: translateY(10px); } }
@keyframes spin   { to  { transform: rotate(360deg); } }

@keyframes rise { 0%{transform:translateY(10vh) scale(.95);opacity:0} 10%{opacity:.5} 100%{transform:translateY(-115vh) scale(1.1);opacity:0} }
@keyframes sway { 0%{transform:translateX(-12px)} 100%{transform:translateX(12px)} }

.grid{
  display:flex;
  flex-wrap:wrap;
  gap:22px;
  justify-content:center;
  align-items:stretch;
  margin-top:20px;
}

.tile{
  width: clamp(250px, 31vw, 330px);          
  background: linear-gradient(180deg, rgba(255,255,255,.04), rgba(255,255,255,.02));
  border:1px solid var(--hair);
  border-radius:14px;
  padding:20px 18px 22px;
  color:#fff;
  text-align:center;
  min-height: 220px; 
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  box-shadow: 0 16px 36px rgba(0,0,0,.34);
  transition: transform .18s ease, box-shadow .18s ease, border-color .18s ease;
  cursor: pointer;
}
.tile:hover{
  transform: translateY(-2px);
  border-color: rgba(168,85,247,.45);
  box-shadow: 0 22px 48px rgba(0,0,0,.45), 0 0 0 6px rgba(168,85,247,.12) inset;
}

/* icon */
.iconWrap{
  width: 52px; height: 52px;
  margin: 0 auto 8px;
  border-radius:12px;
  display:grid; place-items:center;
  background: linear-gradient(180deg, rgba(168,85,247,.22), rgba(34,197,94,.16));
  border:1px solid rgba(255,255,255,.16);
}
.iconWrap .bi{ font-size: 20px; color:#fff; opacity:.95; }

/* text */
.tileTitle{ margin:.25rem 0 .2rem; font-weight:700; font-size:1.12rem; }
.tileDesc { margin:0; color:var(--ink-weak); font-size:.96rem; line-height:1.45; }

/* button */
.openBtn{
  margin-top:12px;
  border:none; color:#fff; font-weight:700;
  padding:.5rem .95rem; border-radius:999px;
  background-image: linear-gradient(135deg, var(--emerald), #15803d);
  box-shadow: 0 10px 22px rgba(21,128,61,.28);
  transition: transform .18s ease, filter .18s ease, box-shadow .18s ease;
  cursor: pointer;
  display: inline-block;
  text-decoration: none;
}
.openBtn:hover{ transform: translateY(-1px); filter:brightness(1.05); }

.backBtn {
  position: absolute;
  top: 20px;
  left: 20px;
  border: none;
  background: rgba(255, 255, 255, 0.1);
  color: white;
  padding: 8px 16px;
  border-radius: 999px;
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
  backdrop-filter: blur(10px);
  text-decoration: none;
  z-index: 10;
}

.backBtn:hover {
  background: rgba(255, 255, 255, 0.15);
  transform: translateX(-4px);
  color: var(--ink);
  text-decoration: none;
}

.contentPage {
  background: linear-gradient(180deg, rgba(255,255,255,.04), rgba(255,255,255,.02));
  border: 1px solid var(--hair);
  border-radius: 14px;
  padding: 30px;
  color: var(--ink);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  box-shadow: 0 16px 36px rgba(0,0,0,.34);
  min-height: 600px;

}

.contentHeader {
  text-align: center;
  margin-bottom: 30px;
}

.contentHeader h2 {
  margin: 0;
  color: var(--ink);
  font-weight: 800;
  letter-spacing: .2px;
  font-size: 2rem;
}

.contentHeader p {
  margin: 0.5rem 0 0;
  color: var(--ink-weak);
  font-size: 1.1rem;
}

/* responsive touch-ups */
@media (max-width: 720px) {
  .backBtn {
    position: relative;
    top: auto;
    left: auto;
    margin-bottom: 12px;
    font-size: 0.9rem;
    display: inline-flex;
    justify-content: flex-start; 
  }

  .inner {
    text-align: left;
  }

  .header {
    margin-top: 0;
    margin-bottom: 25px;
  }
}